import axios from 'axios'
import store from '../store'
import router from '../router'

const instance = axios.create({
  baseURL: process.env.VUE_APP_API_URL,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json'
  }
})

let timerId

instance.interceptors.request.use(

  function (config) {
    timerId = setTimeout(() => store.commit('showLoader', true), 1000)
    config.headers.auth = `Bearer ${localStorage.getItem('access')}`
    return config; 
  }, 

  function (error) {
    clearTimeout(timerId)
    store.commit('showLoader', false)
    return Promise.reject(error)
  }
  
)

instance.interceptors.response.use(

  (response) => {
    clearTimeout(timerId)
    store.commit('showLoader', false)
    if (response?.status == 202) store.commit('showMessage', { 
      color: '#4da64d', 
      icon: 'done', 
      title: 'Успішно', 
      text: response?.data?.message
    })
    return response;
  },

  async (error) => {
    clearTimeout(timerId)
    store.commit('showLoader', false)

    if (error.response?.status == 403) router.push('confirm')

    else if (error.response?.status == 401 && error.config) {
      //error.config.retry = true
      //error.config.headers.refresh = `Bearer ${localStorage.getItem('refresh')}`
      //console.log(error.config)
      try {
        //const { config } = error
        const res = await axios.get(process.env.VUE_APP_API_URL + '/refresh', { 
          headers: { refresh: localStorage.getItem('refresh') }, 
          withCredentials: true 
        })
        localStorage.setItem('access', res.data?.accessToken)
        localStorage.setItem('refresh', res.data?.refreshToken)
        return instance.request(error.config)
        // return new Promise((resolve, reject) => {
        //   setTimeout(_ => resolve(instance(error.config), 500))
        // })
      }
      catch(_) { router.push('/auth') } 
      throw error
    }
    else store.commit('showMessage', { 
      color: '#ff4d4d',
      title: 'Помилка',
      icon: error.response?.data?.icon || 'hourglass_empty', 
      text: error.response?.data?.message || "Немає зв'язку із сервером. Спробуйте зайти пізніше"
    })
    return Promise.reject(error)
  }

)

export default instance